.paginator {
    display: flex;
    justify-content: center;
    padding: 10px;
}

.userItem {
    padding: 10px;
    border: 1px solid white;

}

.selectedPage{
    font-weight: bold;
    text-decoration: underline;
}

.paginator button {
    color: #1D9AF2;
    background-color: #292D3E;
    border: 1px solid #1D9AF2;
    border-radius: 4px;
    padding: 0 15px;
    cursor: pointer;
    height:32px;
    font-size:14px;
    transition: all 0.2s ease-in-out;
}
.paginator button:hover {
    transform: scale(1.1);
}
