.users {
    padding: 10px;
}

.userItem {
    padding: 10px;
    border: 1px solid white;

    }

.users img {
    width: 100px;
}

.selectedPage{
    font-weight: bold;
}

.users  button {
    color: #1D9AF2;
    background-color: #292D3E;
    border: 1px solid #1D9AF2;
    border-radius: 4px;
    padding: 0 15px;
    cursor: pointer;
    height:32px;
    font-size:14px;
    transition: all 0.2s ease-in-out;
}
.users button:hover {
    transform: scale(1.1);
}


